
import { defineComponent } from "vue";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import { PAGE_SIZE } from "@/shared/constants/common.const";
import '@vuepic/vue-datepicker/dist/main.css'
import { RPT203000311_RES, RPT203000311_REQ, CustomerOnboardingReport } from "@/shared/type/RPT202001SS13.type"
import { ExportExcel } from "@/shared/services/ExportExcelService";
import { ExportSheet } from "@/shared/type/ExportExcel.type";
const excel = new ExportExcel()

export default defineComponent({
    name: "rpt202001ss13",
    components: { Pagination, VueDatePicker },

    data() {
        return {
            date: '' as unknown,
            header: [
                { columnName: 'No.' },
                { columnName: 'Reference No.' },
                { columnName: 'Branch Code' },
                { columnName: 'Biz CID' },
                { columnName: 'Biz Name' },
                { columnName: 'No of Account' },
                { columnName: 'No of User' },
                { columnName: 'Email ID' },
                { columnName: 'Phone Number' },
                { columnName: 'Address' },
                { columnName: 'Biz Status' },
                { columnName: 'Status' },
                { columnName: 'Created By' },
                { columnName: 'Created Date' },
                { columnName: 'Authorized By' },
                { columnName: 'Authorized Date' },
                { columnName: 'Updated By' },
                { columnName: 'Updated Date' },
                { columnName: 'Authorized By(BackEnd)' },
                { columnName: 'Authorized Date(BackEnd)' },
            ],
            table: [] as CustomerOnboardingReport[],
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            isResetPage: false,
        }
    },

    computed: {
        formattedDate(): string {
            return this.date ? this.$datetime.formatVueDatePicker(this.date as Date) : ''
        },
    },

    mounted() {
        this.getCustomerOnboardingDetail(false);
    },

    methods: {
        async getCustomerOnboardingDetail(isSearch: boolean) {
            const req = {
                firstRegisterDate: this.date ? this.$datetime.convertDateToString(this.date as Date) : '',
                lastChangeDate: this.date ? this.$datetime.convertDateToString(this.date as Date) : '',
                pageSize: this.pageSize,
                pageNumber: isSearch ? 0 : this.currentPage
            } as RPT203000311_REQ

            if (isSearch) {
                this.currentPage = 1
                this.isResetPage = true;
            }

            const res = await this.$request.getRequest("RPT20300311", req, false) as RPT203000311_RES;

            this.table = this.onMappingTableData(res.body.list)
            this.totalItems = res.body.totalCount
        },

        async exportToExcel() {
            const customHeader = [
                'Reference No',
                'Branch Code',
                'Biz CID',
                'Biz Name',
                'No of Account',
                'No of User',
                'Email ID',
                'Phone Number',
                'Address',
                'Biz Status',
                'Status',
                'Created By',
                'Created Date',
                'Authorized By',
                'Authorized Date',
                'Updated By',
                'Updated Date',
                'Authorized By(BackEnd)',
                'Authorized Date(BackEnd)'
            ]

            const req = {
                firstRegisterDate: this.date ? this.$datetime.convertDateToString(this.date as Date) : '',
                lastChangeDate: this.date ? this.$datetime.convertDateToString(this.date as Date) : '',
                pageSize: this.totalItems,
                pageNumber: 0,
            }
            const res = await this.$request.getRequest("RPT20300311", req, false) as RPT203000311_RES;

            const tableForExport = this.onMappingTableData(res.body.list)

            const dataForExport: ExportSheet = {
                data: tableForExport,
                columnHeader: [customHeader],
                sheetName: 'Customer Onboarding Report'
            }
            excel.exportSheet([dataForExport], 'Customer Onboarding Report')
        },

        onMappingTableData(lists: CustomerOnboardingReport[]): CustomerOnboardingReport[] {
            return lists.map((item: CustomerOnboardingReport) => ({
                referenceNo: item.referenceNo,
                applicationBranchCode: item.applicationBranchCode,
                customerNo: item.customerNo,
                companyName: item.companyName,
                accountCount: item.accountCount,
                userCount: item.userCount,
                email: item.email,
                cellPhoneNo: this.$format.formatPhoneNumber(item.cellPhoneNo),
                customerAddress: item.customerAddress,
                customerStatusCode: `${item.customerStatusCode ? item.customerStatusCode + ' : ' : ''} ${this.$codeConverter.convertValue('CustomerStatusCode', item.customerStatusCode)}`,
                authorizationStatusCode: item.authorizationStatusCode,
                firstRegisterTellerID: item.firstRegisterTellerID,
                firstRegisterDate: this.$datetime.formatStringToStringDate(item.firstRegisterDate),
                authorizerEmployeeNo: this.$datetime.formatStringToStringDate(item.authorizerEmployeeNo),
                authorizationDate: item.authorizationDate,
                lastChangeTellerID: this.$datetime.formatStringToStringDate(item.lastChangeTellerID),
                lastChangeDate: this.$datetime.formatStringToStringDate(item.lastChangeDate),
                lastAuthorizationDate: this.$datetime.formatStringToStringDate(item.lastAuthorizationDate),
                lastAuthorizerEmployeeNo: this.$datetime.formatStringToStringDate(item.lastAuthorizerEmployeeNo)
            }));
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.getCustomerOnboardingDetail(false);
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleReset() {
            this.date = '' as unknown;
            this.currentPage = 1
            this.isResetPage = true
            this.getCustomerOnboardingDetail(false);
        }
    }
})
